import React, { Component } from "react";
import axios from "axios";
import SMLCreator from "./SMLCreator";
import './Style.css';


class Contact extends Component {

    constructor() {
        super();
        this.state = {
          smlData: [],
        };
      }

      componentDidMount() {
        axios.get("json/sml.json").then((res) => {
          this.setState({ smlData: res.data });
        });
      }

      //fill with info

      smlFiller() {
        let { smlData } = this.state;
        return smlData.map((data) => {
          return (
            <SMLCreator link={data.link} icon={data.icon} id={data.id} key={data.id}/>
          );
        });
      }

  render() {
    return (
      <section id="contact">
        <div className="row">
          <div className="contact__left">
                <h1 className="titleFontSize">Let's Talk</h1>
                <h5 className="email">mail-us@gmail.com</h5>
                <p className="normalFontSize">
                I wiil very happy if you told me what you love in my site and what
                you hate. thanks for visit.
                </p>
                <ul className="sml__list" >{this.smlFiller()}</ul>
          </div>

          <div className='contact__right'>
            <form id='contact__form'>
              <input id='contact__name' placeholder='Your name here' type='text' />
              <input id='contact__email' placeholder='Your email here' type='email' />
              <input id='contact__subject' placeholder='Your subject here' type='text' />
              <textarea id="contact__message" cols="30" rows="2" placeholder="Your message here"></textarea>
              <button type='submit' className='btn'>Send</button>
            </form>
          </div>
        </div>
      </section>
    );
  }
}

export default Contact;
