import React, {useEffect} from "react";
import $ from 'jquery';

function TeamCreator(props) {

  useEffect(() => {
    //get all projects container
    var elements = document.getElementsByClassName("team__container");

    //show elements
    var show = function(event) {
      var a = document.querySelector(`#${event.currentTarget.id} .team_info`);
      $(a).fadeIn(300);
      $(a).css('display', 'grid');
    };

    var hide = function(event) {
      var a = document.querySelector(`#${event.currentTarget.id} .team_info`);
      $(a).fadeOut(300);

    };

    //hide elements
    $(document).on('touchstart', (event) => {
      if (!$(event.target).closest('.team__container').length) {
        $(`.team_info`).fadeOut(300);
      }
    });

    for (var i = 0; i < elements.length; i++) {
      elements[i].addEventListener('click', show, false);
      elements[i].addEventListener('mouseenter', show, false);
      elements[i].addEventListener('mouseleave', hide, false);
    }

  }, []);

  return (<div className="team__container" id={props.id}>
    <div className='team_info'>
      <h3 className='team_name'>{props.name}</h3>
      <h6 className='team_job'>{props.job}</h6>
      <ul>
        <li >
          <a rel="noreferrer" target='_blank' className='team_socialLink' href={props.facebook}>
            <i className={props.facebookLogo}></i>
          </a>
        </li>
        <li >
          <a rel="noreferrer" target='_blank' className='team_socialLink' href={props.behance}>
            <i className={props.behanceLogo}></i>
          </a>
        </li>
        <li >
          <a rel="noreferrer" target='_blank' className='team_socialLink' href={props.instagram}>
            <i className={props.instagramLogo}></i>
          </a>
        </li>
      </ul>
    </div>
    <img src={props.img} alt='team' className='team__img'/>
  </div>);

}

export default TeamCreator;
