import React, { Component } from "react";
import { Link } from "react-router-dom";
import './Style.css';
import './ErrorNav.css';

class ErrorNav extends Component {



  render() {
    return (
      <header id="navbar">
        <div className="row">
          <nav className='navbar__pc'>
          <div className="navbar__logo">
            <a href="//imweb.netlify.app">IM WEB</a>
          </div>

          <div className="navbar__links">
            <ul>
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <a href='//imweb.netlify.app/#services'>Services</a>
              </li>
              <li>
                <a href='//imweb.netlify.app/#work'>Work</a>
              </li>
              <li>
                <a href='//imweb.netlify.app/#team'>Team</a>
              </li>
              <li>
                <a href='//imweb.netlify.app/#contact'>Contact</a>
              </li>
              <li>
                <a href='//facebook.com'>
                  <i className="bx bxl-facebook"></i>
                </a>
              </li>
              <li>
                <a href='//instagram.com'>
                  <i className="bx bxl-instagram"></i>
                </a>
              </li>
              <li>
                <a href='http://behance.net/ibrahimmaded'>
                  <i className="bx bxl-behance"></i>
                </a>
              </li>
            </ul>
          </div>
          </nav>

          <nav className='navbar__mobile'>
            <div className="navbar__logo">
              <a href="//imweb.netlify.app">IM WEB</a>
            </div>

            <div className="navbar__mobile--menu" >
              <a href='/#'>
                <i className="bx bx-menu"></i>
              </a>
            </div>
          </nav>
        </div>
      </header>
    );
  }
}

export default ErrorNav;
