import React, {useEffect} from "react";
import $ from 'jquery';

function ProjectsCreator(props) {

  useEffect(() => {
    //get all projects container
    var elements = document.getElementsByClassName("projects__container");

    //show elements
    var show = function(event) {
      var a = document.querySelector(`#${event.currentTarget.id} .projects__infoAndLink`);
      $(a).fadeIn(300);
      $(a).css('display' , 'grid');

    };

    //hide elements
    $(document).on('touchstart' ,(event) => {
      if (!$(event.target).closest('.projects__container').length) {
        $(`.projects__infoAndLink`).fadeOut(300);
      }
    });

    var hide = function(event) {
      var a = document.querySelector(`#${event.currentTarget.id} .projects__infoAndLink`);
      $(a).fadeOut(300);

    };

    //add event listener
    for (var i = 0; i < elements.length; i++) {
      elements[i].addEventListener('click', show, false);
      elements[i].addEventListener('mouseenter', show, false);
      elements[i].addEventListener('mouseleave', hide, false);
    }

  }, []);

  return (<div className="projects__container" id={props.id}>
    <div className="projects__infoAndLink">
      <h1>{props.info}</h1>
      <a className='btn' href={props.link} rel="noreferrer" target='_blank'>Visit</a>
    </div>
    <img src={props.img} alt="project"/>
  </div>);
}

export default ProjectsCreator;
